@keyframes scrollOpen {
    from {height: 0%;}
    to {height: 100%;}
}

.dealerContainer {
    background-image: url(../../assets/bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    border: .25rem solid #202020;
    border-radius: .35rem;
    overflow: hidden;

    width: 45%;
    height: 95%;

    display: flex;
    flex-direction: column;
    
    > .vehiclesContainer {
        margin: .5rem 1rem 1rem 1rem;
        overflow-y: scroll;
        &::-webkit-scrollbar {
            display: none;
        }
    }
    > .searchBar {
        display: flex;
        align-items: center;
        margin: 1rem 1rem 0 1rem;

        > .searchField {
            margin-right: .75rem;
            margin-left: 0.5rem;
            border-radius: 0;
            border: none;
            width: 100%;
            font-size: 1rem;
            font-family: "Roboto", "Helvetica", "Arial", sans-serif;
            font-weight: 400;
            line-height: 1.1876em;
            letter-spacing: 0.00938em;
            &:focus {
                outline: none;
            }
        }
    }
    > .dealerNav {
        margin-top: auto;
        padding: 0;

        > .MuiBottomNavigationAction-root {
            padding-top: .5rem;
        }
        > .MuiBottomNavigationAction-root.Mui-selected {
            color: #202020;
        }
    }
}